.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

input[type="text"] {
  height: 40px;
  line-height: 30px;
  padding: 5px 15px;
  border: none;
  box-shadow: none;
  box-sizing: border-box;
  color: #666;
  min-width: 300px;
}

button {
  padding: 10px 20px;
  line-height: 20px;
  box-shadow: none;
  border: none;
  background: blue;
  color: white;
  box-sizing: border-box;
  margin-top: 10px;
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
